<template>
  <div id="result" class="wrap" v-for="squre in squreData.filter((v)=> v.squre === this.focus)" :key="squre">
    <strong>{{ squre.title }}</strong>
    <div class="img_box">
      <img :src="squre.imgSrc">
    </div>
    <pre>{{ squre.content }}</pre>
  </div>
</template>

<script>
import squreData from '@/assets/squreData';

export default {
  props : ["focus"],
  data () {
    return {
      squreData,
    }
  },
  mounted() {
    const wrap = document.getElementById('result');
  }
}
</script>

<style lang="scss" scoped>
    .wrap {width: 400px; color: #444; border-radius: 20px 0 0 0; display: flex; align-items: center; flex-wrap: wrap; flex-direction: column; 
      justify-content: center; animation: introAll .5s ease;  position: absolute; right: 50px; z-index: 1;
      .img_box {max-width: 300px; padding: 0 100px; height: 300px; display: flex ; align-items: center; justify-content: center;
        img {width: 100%; max-height: 100%;}
      }
      strong {display: block; font-weight: bold; width: 100%; color: #000; border-radius: 20px 0 0 0; height: 50px; line-height: 50px;}
      pre {white-space: pre-wrap; height: 300px;}
    }
      
  @keyframes introAll {
    0% {
      opacity: 0;
          color: #e6c4d5; margin-top: -50px;
    }
    100% {
      opacity: 1; margin-top: 0;
    }
  }
</style>